import { buttonVariants } from "../ui/button";
import Link from "next/link";

export const HeaderLinks = async () => {
  const checkUserSignIn = async () => {
    try {
      const session = await fetch(`/api/auth`);
      return session.ok;
    } catch (error) {
      return false;
    }
  };

  const isSignedIn = await checkUserSignIn();

  return (
    <div className="inspect flex h-14 w-[100%] items-center justify-center gap-2 whitespace-nowrap font-geistmono text-sm">
      <Link
        className={`${buttonVariants({ variant: "default" })} mb-10`}
        href="/route/overview"
      >
        {isSignedIn ? "Sign out" : "Sign in"}
      </Link>
    </div>
  );
};
